:root {
  --main-bg-color: #fffff9;
  --secondary-bg-color: #fef3e4;
  --main-text-color: #2e2520;
  --accent-text-color: #876655;
  --icon-color: #674d40;
  --main-accent: #ab816c;
  --light-accent: #fff;
  --dark-accent: #000;
  --main-font: 'Montserrat', sans-serif;
  --decorative-font: 'Caveat Brush', cursive;
}

html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  font-family: var(--main-font), sans-serif;
  font-size: 18px;
  line-height: 1.22;
  background-color: var(--main-bg-color);
  color: var(--main-text-color);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
}

.link {
  text-decoration: none;
  color: inherit;
}

.link:hover,
.link:focus {
  color: var(--accent-text-color);
}

.list {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.conteiner {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 767px) {
  .conteiner {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (min-width: 768px) {
  .conteiner {
    max-width: 634px;
  }
}

@media screen and (min-width: 1280px) {
  .conteiner {
    max-width: 822px;
  }
}

.button {
  font-family: var(--decorative-font);
  font-size: 18px;
  line-height: 1.21;
  text-align: center;
  border: none;
  color: var(--main-bg-color);
  background-color: var(--main-accent);
  border-radius: 25px;
  padding-left: 46px;
  padding-right: 46px;
  padding-top: 5px;
  padding-bottom: 5px;
}

@media screen and (max-width: 767.98px) {
  .button {
    margin-top: 37px;
  }
}

@media screen and (max-width: 1279px) {
  .button {
    margin-top: 26px;
  }
}

@media screen and (min-width: 1280px) {
