    font-size: 24px;
    line-height: 1.21;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 44px;
    padding-right: 44px;
    margin-top: 30px;
  }
}

input {
  border-top: 0;
  border-right: 0;
  border-left: 0;
  border-bottom: 1px solid #ab816c;
}

.social-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.social-list .icon {
  height: 19px;
  width: 24px;
  fill: var(--main-text-color);
